import classNames from 'classnames';
import React from 'react';
import InputMask from 'react-input-mask';
import { formInputStylesDark, formInputStylesLight } from '../../styles/common-styles';

export interface TextFieldProps extends React.ComponentPropsWithRef<'input'> {
  readonly id: string;
  readonly mask?: string;
  readonly color?: 'dark' | 'light';
}

export const TextField: React.FC<TextFieldProps> = React.forwardRef<
  HTMLInputElement,
  TextFieldProps
>(({ children, id, mask, color = 'dark', className, ...props }, ref) => {
  const classes = classNames(
    'focus:outline-none focus:ring disabled:opacity-50 disabled:cursor-default',
    color === 'dark' ? formInputStylesDark : formInputStylesLight,
    className
  );
  return mask != null ? (
    <InputMask ref={ref} id={id} mask={mask} className={classes} {...props} />
  ) : (
    <input ref={ref} id={id} type="text" className={classes} {...props}>
      {children}
    </input>
  );
});

TextField.displayName = 'TextField';
